<template>
    <div class="thank_you">
        <h1>Dziękujemy za złożenie zamówienia!</h1>
        <div style="display: flex; gap: 3em; justify-content: center;">
            <div>
                <h2>Dane do rachunku: </h2>
                <div v-for="(data, index) in $store.state.PersonalData" :key="index">
                <p>{{ data }}</p>
                </div>
            </div>
            <div>
                <h2>Wybrana opcja odbioru: </h2>
                <p>{{ $store.state.DeliveryChoice }}</p>
               <span v-if="$store.state.DeliveryChoice === 'wysyłka'" > Cena: 5</span>
            </div>
            <div v-if="$store.state.DeliveryChoice === 'Wysyłka'">
                <h2>Dane do wysyłki: </h2>
                <div v-for="(delivery, index) in $store.state.Delivery" :key="index">
                <p>{{ delivery }}</p>
                </div>
            </div>
        </div>
        <h1>Produkt: </h1>
        <div style="display: flex; gap: 3em; justify-content: center;">
            <div v-if="$store.state.Front.boolean">
                <h2>Przód:</h2>
                <p>Cena: 10</p>
                <img :src="$store.state.Front.url">
                <h3>Wybrane efekty:</h3>
                <p v-if="$store.state.Front.blur.boolean">Blur {{ $store.state.Front.blur.scale }} Cena: 3</p>
                <p v-if="$store.state.Front.gray.boolean">Grayscale {{ $store.state.Front.gray.scale }} Cena: 2</p>
            </div>
            <div v-if="$store.state.Back.boolean">
                <h2>Tył:</h2>
                <p>Cena: 10</p>
                <img :src="$store.state.Back.url">
                <h3>Wybrane efekty:</h3>
                <p v-if="$store.state.Back.blur.boolean">Blur {{ $store.state.Back.blur.scale }} Cena: 3</p>
                <p v-if="$store.state.Back.gray.boolean">Grayscale {{ $store.state.Back.gray.scale }}  Cena: 2</p>
            </div>
        </div>

    </div>
</template>
<script>
export default {
  data: () => ({
    order: {
      date: '',
      total: 0,
      product: {
        front: {
          boolean: false,
          blur: {
            scale: 0,
            boolean: false
          },
          gray: {
            scale: 0,
            boolean: false
          },
          img: ''
        },
        back: {
          boolean: false,
          blur: {
            scale: 0,
            boolean: false
          },
          gray: {
            scale: 0,
            boolean: false
          },
          img: ''
        }
      },
      shipingMethod: '',
      personalData: {
      },
      deliveryData: {
      }
    }
  }),
  mounted () {
    const data = new Date()
    this.order.total = this.$store.state.Total
    this.order.product.front.boolean = this.$store.state.Front.boolean
    this.order.product.front.gray.boolean = this.$store.state.Front.gray.boolean
    this.order.product.front.gray.scale = this.$store.state.Front.gray.scale
    this.order.product.front.blur.boolean = this.$store.state.Front.blur.boolean
    this.order.product.front.gray.scale = this.$store.state.Front.gray.scale
    this.order.product.front.img = this.$store.state.Front.url
    this.order.product.back.boolean = this.$store.state.Back.boolean
    this.order.product.back.gray.boolean = this.$store.state.Back.gray.boolean
    this.order.product.back.gray.scale = this.$store.state.Back.gray.scale
    this.order.product.back.blur.boolean = this.$store.state.Back.blur.boolean
    this.order.product.back.gray.scale = this.$store.state.Back.gray.scale
    this.order.product.back.img = this.$store.state.Back.url
    this.order.personalData = this.$store.state.PersonalData
    this.order.deliveryData = this.$store.state.Delivery
    this.order.shipingMethod = this.$store.state.DeliveryChoice
    this.order.date = data.toString()
    console.log(this.order)
  }
}
</script>
<style lang="sass" scoped>
  .thank_you
    display: inline
p
  font-family: sans-serif
  font-weight: bold
img
  height: 10em
  width: 10em
@media only screen and (max-width: 780px)
  .thank_you
    display: grid !important
    div
      display: grid !important
</style>
